#navigationother {
  width: 640px;
  float: left;
  margin: 0 auto;
}
#navigationother2 {
  height: 20px;
  line-height: 20px;
  background: #FEE3FD;
  margin: 0px;
  text-align: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-top: 2px solid #FBFEEA;
  margin-bottom: 20px;
}
#navigationother2 a {
  background: transparent;
  font: 12px monospace, georgia;
  text-transform: uppercase;
  font-style: normal;
  line-height: 20px;
  padding: 4px 8px;
  letter-spacing: 3px;
  margin-right: 6px;
  color: #9D6AA6;
  text-decoration: none;
}
#navigationother2 a:hover {
  color: #999;
}

/* About é¡µé¢ - å·¦ä¾§ç«–å‘å¯¼èˆª */
.about-page-container {
  display: flex;
  width: 640px;
  margin: 0 auto;
  /*   font-family: monospace, sans-serif; */
}

.about-sidebar {
  width: 80px;
  flex-shrink: 0;
  margin-right: 10px;
  margin-top: 0.5rem;
}

.about-content {
  margin-left: 10px;
  flex: 1;
}

.about-nav-container {
  width: 100%;
}

.about-nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.about-nav-item {
  margin-bottom: 8px;
}

.about-nav-link {
  display: block;
  background: #FEE3FD;
  color: #9D6AA6;
  text-decoration: none;
  font: 12px monospace, georgia;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 6px 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-top: 2px solid #FBFEEA;
  transition: color 0.3s ease;
}

.about-nav-link:hover {
  color: #999;
}

